import React, { useContext, useCallback } from 'react';

import { useTheme, Typography, Grid } from '@material-ui/core';
import _ from 'lodash';

import { useSpring, animated as a, interpolate } from 'react-spring';

import { wrapPage } from '../../imports/wrap-page';

export default wrapPage(() => {
  const [{ xys, st }, setSpring] = useSpring(() => ({ st: 0, xys: [0, 0, 1], config: { mass: 5, tension: 350, friction: 40 } }));
  const onScroll = useCallback(e => setSpring({ st: e.target.scrollTop / e.target.scrollHeight }), []);

  const item1 = interpolate([st, xys], (st, xys) => `scale(${st}) perspective(600px) rotateX(${xys[0]}deg) rotateY(${xys[1]}deg)`);
  const item2 = interpolate([st, xys], (st, xys) => `rotate(${st * 1000}deg)`);
  const item3 = interpolate([st, xys], (st, xys) => `translateY(${-(st * 20000) + 600}px)`);
  const item4 = interpolate([st, xys], (st, xys) => `translateY(${-(st * 3000) + 0}px)`);
  const item5 = interpolate([st, xys], (st, xys) => `scaleX(${st * 10})`);

  return (
    <>
      <div
        onScroll={onScroll}
        style={{
          position: 'fixed',
          left: 0, top: 0,
          width: '100%',
          height: '100%',
          overflow: 'scroll',
        }}
      >
        <Grid container justify="center">
          <Grid item xs={4}>
            <a.div style={{ transform: item2, height: 300, background: 'red' }}></a.div>
          </Grid>
        </Grid>
        <Grid container justify="flex-start">
          <Grid item xs={4}>
            <a.div style={{ transform: item3, height: 300, background: 'blue' }}></a.div>
          </Grid>
        </Grid>
        <Grid container justify="flex-end">
          <Grid item xs={4}>
            <a.div style={{ transform: item4, height: 300, background: 'green' }}></a.div>
          </Grid>
        </Grid>
        <Grid container justify="center">
          <Grid item xs={4} style={{ overflow: 'hidden' }}>
            <a.div style={{ transform: item5, transformOrigin: 'center left', height: 100, background: 'yellow' }}></a.div>
          </Grid>
        </Grid>
        <div
          style={{
            height: 2000,
          }}
        ></div>
        <a.div
          onMouseMove={({ clientX: x, clientY: y }) => setSpring({ xys: [-(y - window.innerHeight / 2) / 20, (x - window.innerWidth / 2) / 20, 1.1] })}
          onMouseLeave={() => setSpring({ xys: [0, 0, 1] })}
          style={{
            transform: item1,
            position: 'fixed',
            top: 'calc(50% - 150px)',
            left: 'calc(50% - 150px)',
            width: 300,
            height: 300,
            background: 'gray',
          }}
        ></a.div>
      </div>
    </>
  );
});
